.weather-card{background:none;max-width:600px;min-width:400px;border:none}.weather-card .title{font-family:"Montserrat",sans-serif;font-style:normal;color:#fff;text-shadow:0.5px 0.5px black,-0.5px -0.5px black}.weather-card .title .location{font-weight:bold;font-size:44px;text-transform:uppercase}.weather-card .title .date{font-weight:normal;font-size:24px;text-transform:uppercase}.weather-card .weather-today .temperature{max-width:300px;font-size:calc(500% + 10vw);font-family:"Montserrat",sans-serif;font-style:normal;color:#fff;white-space:nowrap;text-shadow:0.5px 0.5px black,-0.5px -0.5px black}.weather-card .weather-today .weather-pic img{width:30vw;height:15vw;min-width:200px;min-height:200px;max-width:300px;max-height:300px;right:0px}.weather-card .weather-today .weather-description{font-size:20px;font-family:"Montserrat",sans-serif;font-style:normal;color:#fff;text-transform:uppercase;white-space:nowrap;text-shadow:0.5px 0.5px black,-0.5px -0.5px black}.weather-card .weather-today .weather-description h5{white-space:normal}.weather-card .weather-days .day-title{font-family:"Montserrat",sans-serif;font-style:normal;color:#fff;text-shadow:0.5px 0.5px black,-0.5px -0.5px black;font-size:16px;text-transform:capitalize}.weather-card .weather-days .day-temp{font-family:"Montserrat",sans-serif;font-style:normal;color:#fff;text-shadow:0.5px 0.5px black,-0.5px -0.5px black;font-size:20px}.weather-card .weather-days .day-pic{width:10vw;min-width:75px;min-height:75px;max-width:100px;max-height:100px}.card-map{width:35vw;min-width:350px;max-width:700px;margin-right:0}.card-map .map-container-5{overflow:hidden;position:relative;height:0}.card-map .map-container-5 .map .js-map{width:100%;height:100%;left:0;top:0;position:absolute}
