.weather-control{font-family:Montserrat,sans-serif;font-weight:bold;font-size:14px;color:#fff;background-color:none}.weather-control .select-wrapper{max-width:77px;min-width:71px}.weather-control .select-wrapper .select-language{font-family:Montserrat,sans-serif;font-weight:bold;font-size:14px;color:#fff;vertical-align:top;text-align:left;min-width:71px;height:44px;background-color:rgba(174,181,185,0.7);border:none;padding:.5rem 1rem;margin:0px;background-image:url(src/icons/arrow-down.svg);background-repeat:no-repeat;background-size:1rem 1rem}.weather-control .select-wrapper .select-language.option-language{background-color:rgba(174,181,185,0.7);height:30px}.weather-control .degree{margin-left:15px;float:left}.weather-control .degree .btn{width:44px;height:44px;background-color:rgba(76,82,85,0.4);color:rgba(255,255,255,0.4);border:none;opacity:1}.weather-control .degree .btn .active{background-color:rgba(174,181,185,0.7);color:#fff}.weather-control .refresh-bg{width:44px;height:44px;float:left;background-color:rgba(174,181,185,0.7);background-image:url(src/icons/refresh-bg.svg);background-repeat:no-repeat;border:none}.weather-control .search{margin-top:17px;min-width:120px}.weather-control .search .search-input{opacity:0.5;height:44px;font-family:Montserrat,sans-serif;font-weight:normal;font-size:14px}.weather-control .search .search-button{height:44px;font-family:Montserrat,sans-serif;font-weight:bold;font-size:14px;color:#fff;background-color:rgba(174,181,185,0.7);border:none}
